import React from 'react';
import logo from './logo.svg';
import './App.css';
import HomePage from './paging/homepage'
import AboutPage from './paging/aboutpage'
import ArticlesListPage from './paging/articleslistpage'
import Article from './paging/articlepage'
import { 
  BrowserRouter as Router, Route, Switch
} from 'react-router-dom'
import NavBar from './navbar'
import NotFoundPage from './paging/notfoundpage'

function App() {
  return (
    <Router>
    <div className="App">
      <NavBar></NavBar>
      <div id="page-body">
        <Switch>
        <Route path="/" component={HomePage} exact />
      <Route path="/about" component={AboutPage}  />
      <Route path="/articles" component={ArticlesListPage}  />
      <Route path="/article/:name" component={Article}  />
      <Route component={NotFoundPage}></Route>
        </Switch>
      
      </div>
    </div>
    </Router>
  );
}

export default App;
